<template>
  <el-container class="effect-state-container">
    <el-main>
      <h1 class="home-title">State-Effect</h1>
      <div style="display: flex;flex-direction: row-reverse;margin-top: 20px;">
        <el-button type="primary" @click="dialogVisible = true" style="margin-right: 30px;margin-bottom: 10px;">CONFIRM</el-button>
      </div>
      <el-alert style="margin-top:10px;margin-bottom:20px;" v-if="radio == 'Always'" title="[Effect] should always happen while [state1, ..., staten]." type="success" :closable="false">
      </el-alert>
      <el-alert style="margin-top:10px;margin-bottom:20px;" v-if="radio == 'Never'" title="[Effect] should never happen while [state1, ..., staten]." type="success" :closable="false">
      </el-alert>
      <el-dialog title="CONFIRM PROPERTY" :visible.sync="dialogVisible" center width="50%">
        <el-form ref="strategy" :model="strategy" label-width="20%" :rules="resolve_rule" class="commit-form">
          <el-form-item label="Resolve Strategy" prop="resolve">
            <el-select v-model="strategy.resolve" placeholder="Please Choose the Resolve Strategy">
              <el-option v-for="item in resolveOptions" :key="item.value" :label="item.label" :value="item.value" :title="item.description">
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <span slot="footer">
          <el-button @click="dialogVisible = false">Cancel</el-button>
          <el-button type="primary" @click="Confirm">Confirm</el-button>
        </span>
      </el-dialog>

      <el-row :gutter='5' type="flex">
        <el-col :span='11'>
          <div class='effect-title' style="font-weight: bold;">
            Effect
          </div>
          <el-form ref="effectTemplate" :model="effectTemplate" label-width="10%" :rules="effect_rules">
            <el-form-item label="Location" prop="location">
              <el-select v-model="effectTemplate.location" placeholder="Please Choose the Location" @change="locationChange" style="width: 80%;float:right;margin-right:10px;">
                <el-option v-for="item in locationOptions" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="effect" prop="effect">
              <el-select v-model="effectTemplate.effect" placeholder="Please Choose the Effect" style="width: 80%;float:right;margin-right:10px;">
                <el-option v-for="item in effectsOptions" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-form>
        </el-col>
        <el-col :span='5' class="desc">
          <div style="font-size:15pt;width:99%">
            <el-select v-model="radio" style="width: 70%;" placeholder="Frequency" class="radio">
              <el-option v-for="item in radioOptions" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
            <div style="margin-top:12%;font-weight: bold;">
              happen while
            </div>
          </div>
        </el-col>
        <el-col :span='8'>
          <div class='state-title' style="font-weight: bold;">
            State
          </div>
          <!-- list -->
          <div v-for="(t, i) in states" :key="i">
            <el-card class="card-margin">
              <el-descriptions :title="'State ' + (i + 1)" :column="1" border>
                <template slot="extra">
                  <el-button size="mini" icon="el-icon-edit" @click="updateItem(t, i)">
                  </el-button>
                  <el-button size="mini" icon="el-icon-delete" @click="removeItem(i)"></el-button>
                </template>
                <el-descriptions-item label="state">{{ t.location }}</el-descriptions-item>
                <el-descriptions-item label="state">{{ t.category + '.' + t.state  + '.' + t.value}}</el-descriptions-item>
              </el-descriptions>
            </el-card>
          </div>
          <!--  -->
          <el-card style="display: flex;align-content: center;justify-content: center;" class="card-margin">
            <el-button type="text" @click="addState()">
              <i class="el-icon-circle-plus" style="font-size: 25px;"> Add State</i>
            </el-button>
            <el-dialog title="State" :visible.sync="stateDialog" @close="cancelAddState">
              <el-form ref="statesTemplate" :model="statesTemplate" label-width="20%" :rules="state_rules">
                <el-form-item label="Location" prop="location">
                  <el-select v-model="statesTemplate.location" placeholder="Please Choose the Location" @change="stateLocationChange">
                    <el-option v-for="item in locationOptions" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="category" prop="category">
                  <el-select v-model="statesTemplate.category" placeholder="Please Choose the Category  " @change="statesCategoryChange">
                    <el-option label="device" value="device"></el-option>
                    <el-option label="state" value="state"></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="state" prop="state">
                  <el-select v-if="statesTemplate.category=='device'" v-model="statesTemplate.state" placeholder="Please Choose the Device" @change="statesChange"
                    style="width: 120px;margin-right:20px">
                    <el-option v-for="item in deviceOptions" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                  </el-select>
                  <el-select v-if="statesTemplate.category=='state'" v-model="statesTemplate.state" placeholder="Please Choose the State" @change="statesChange" style="width: 120px;margin-right:20px">
                    <el-option v-for="item in stateOptions" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                  </el-select>
                  <el-select v-model="statesTemplate.value" placeholder="Please Choose the Value">
                    <el-option v-for="item in valueOptions" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                  </el-select>
                </el-form-item>
              </el-form>
              <div slot="footer">
                <el-button @click="cancelAddState">Cancel</el-button>
                <el-button type="primary" @click="confimAddState">Confirm</el-button>
              </div>
            </el-dialog>
          </el-card>
        </el-col>
      </el-row>
    </el-main>
  </el-container>
</template>

<script>
import { resolveOptions, radioOptions } from '../../public/global/data.js'
import axios from 'axios'
export default {
  name: 'EffectState',
  components: {
  },
  data () {
    return {
      radioOptions,
      resolveOptions,
      dialogVisible: false,
      stateDialog: false,
      effectTemplate: {
        effect: '',
        location: ''
      },
      states: [],
      statesTemplate: {
        category: '',
        state: '',
        value: '',
        location: ''
      },
      strategy: {
        resolve: ''
      },
      resolve_rule: {
        resolve: [
          { required: true, message: 'please select the resolve strategy', trigger: 'blur' }
        ]
      },
      state_rules: {
        location: [
          { required: true, message: 'please select the location.', trigger: 'blur' }
        ],
        category: [
          { required: true, message: 'please select the category.', trigger: 'blur' }
        ],
        state: [
          { required: true, message: 'Please select the state.', trigger: 'blur' }
        ]
      },
      effect_rules: {
        location: [
          { required: true, message: 'please select the location.', trigger: 'blur' }
        ],
        effect: [
          { required: true, message: 'please select the effect.', trigger: 'blur' }
        ]
      },
      radio: 'Always',
      stateOptions: [],
      deviceOptions: [],
      effectsOptions: [],
      locationOptions: [],
      valueOptions: [],
      index: '',
      updateFlag: false
    }
  },
  mounted () {
    this.getLocation()
    if (this.$route.params.update >= 0) {
      const temp = this.$route.params.value.split(' & ')
      temp[1] = temp[1].substring(2)
      temp[temp.length - 2] = temp[temp.length - 2].substring(0, temp[temp.length - 2].length - 2)
      const effect = temp[0].split('.')
      this.radio = temp[temp.length - 1]
      this.locationChange(effect[0])
      this.effectTemplate = {
        location: effect[0],
        effect: effect[1]
      }
      for (let i = 1; i < temp.length - 1; i++) {
        const item = temp[i].split('.')
        this.states.push({
          location: item[0],
          category: item[1],
          state: item[2],
          value: item[3]
        })
      }
    }
  },
  methods: {
    updateItem (Info, index) {
      if (Info && index >= 0) {
        const item = JSON.parse(JSON.stringify(Info))
        this.stateDialog = true
        this.statesTemplate = item
        this.inquiryInfor(this.statesTemplate.category)
        this.statesChange(this.statesTemplate.state)
        this.index = index
        this.updateFlag = true
      }
    },
    removeItem (index) {
      this.states.splice(index, 1)
      this.refresh()
    },
    locationChange (val) {
      this.effectTemplate.effect = null
      this.effectsOptions = []
      const that = this
      axios.get('/' + this.URL.url + '/effect_list/' + val, {
        headers: {
          'Content-Type': 'application/text'
        }
      }).then(res => {
        for (let i = 0; i < res.data.length; i++) {
          that.effectsOptions.push({
            value: res.data[i].slice(7, res.data[i].length),
            label: res.data[i].slice(7, res.data[i].length)
          })
        }
      }).catch(err => {
        console.log(err)
      })
    },
    stateLocationChange (val) {
      this.statesTemplate.state = null
      this.statesTemplate.value = null
      this.inquiryInfor(this.statesTemplate.category)
    },
    statesChange (val) {
      this.statesTemplate.value = null
      if (this.statesTemplate.state) {
        this.valueOptions = this.STATE.state[val]
      } else {
        this.valueOptions = []
      }
    },
    getLocation () {
      const that = this
      this.locationOptions = []
      axios.get('/' + this.URL.url + '/room_list', {
        headers: {
          'Content-Type': 'application/text'
        }
      }).then(res => {
        for (let i = 0; i < res.data.length; i++) {
          that.locationOptions.push({
            value: res.data[i],
            label: res.data[i]
          })
        }
      }).catch(err => {
        console.log(err)
      })
    },
    Confirm () {
      this.$refs.effectTemplate.validate((valid1) => {
        this.$refs.strategy.validate((valid2) => {
          if (!valid1) {
            this.$message.warning('Please fill in the form correctly!')
            this.dialogVisible = false
          } else if (!valid2) {
            this.$message.warning('Please fill in the form correctly!')
          } else {
            if (this.states.length < 1) {
              this.$message.warning('Please fill in at least one state!')
              this.dialogVisible = false
              return
            }
            let stateList = this.states[0].location + '.' + this.states[0].state + '.' + this.states[0].value
            let value = this.effectTemplate.location + '.' + this.effectTemplate.effect + ' & ' + '[ ' + this.states[0].location + '.' + this.states[0].category + '.' + this.states[0].state + '.' + this.states[0].value
            let descList = this.states[0].location + '.' + this.states[0].state + '.' + this.states[0].value
            for (let i = 1; i < this.states.length; i++) {
              value = value + ' & ' + this.states[i].location + '.' + this.states[i].category + '.' + this.states[i].state + '.' + this.states[i].value
              descList = descList + ' , ' + this.states[i].location + '.' + this.states[i].state + '.' + this.states[i].value
              stateList = stateList + ' & ' + this.states[i].location + '.' + this.states[i].state + '.' + this.states[i].value
            }
            value = value + ' ]' + ' & ' + this.radio
            const temp = this.ltlMethod.getLTL([this.effectTemplate], this.URL.url)
            temp.then((result) => {
              let ltl = result.substring(3, result.length) + ')' + ' & ' + '(' + stateList + ')'
              if (this.radio === 'Always') {
                ltl = 'G(' + ltl + ')'
              } else {
                ltl = 'F(' + ltl + ')'
              }
              if (this.$route.params.update >= 0) {
                this.PROPERTY.property[this.$route.params.update].value = value
                this.PROPERTY.property[this.$route.params.update].strategy = this.strategy.resolve
                this.PROPERTY.property[this.$route.params.update].description = 'When [ ' + descList + ' ] , [ ' + this.effectTemplate.location + '.' + this.effectTemplate.effect + ' ] will [ ' + this.radio + ' ] happen.'
                this.PROPERTY.property[this.$route.params.update].ltl = ltl
              } else {
                this.PROPERTY.property.push(
                  {
                    template: 'effect-state',
                    value: value,
                    description: 'When [ ' + descList + ' ] , [ ' + this.effectTemplate.location + '.' + this.effectTemplate.effect + ' ] will [ ' + this.radio + ' ] happen.',
                    ltl: ltl,
                    strategy: this.strategy.resolve
                  }
                )
              }
              this.$router.back()
            })
          }
        })
      })
    },
    inquiryInfor (val) {
      const that = this
      if (val === 'device') {
        this.deviceOptions = []
        axios.get('/' + this.URL.url + '/room_device/' + that.statesTemplate.location, {
          headers: {
            'Content-Type': 'application/text'
          }
        }).then(res => {
          for (let i = 0; i < res.data.length; i++) {
            const data = res.data[i].slice(0, -3)
            // var temp = data[0:-3]
            that.deviceOptions.push({
              value: data,
              label: data
            })
          }
        }).catch(err => {
          console.log(err)
        })
      } else if (val === 'state') {
        this.stateOptions = []
        axios.get('/' + this.URL.url + '/room_state/' + that.statesTemplate.location, {
          headers: {
            'Content-Type': 'application/text'
          }
        }).then(res => {
          for (let i = 0; i < res.data.length; i++) {
            that.stateOptions.push({
              value: res.data[i],
              label: res.data[i]
            })
          }
        }).catch(err => {
          console.log(err)
        })
      }
    },
    statesCategoryChange (val) {
      this.statesTemplate.state = null
      this.statesTemplate.value = null
      this.inquiryInfor(val)
    },
    addState () {
      this.stateDialog = true
      this.updateFlag = false
    },
    cancelAddState () {
      this.stateDialog = false
      this.statesTemplate = {
        category: '',
        state: '',
        value: '',
        location: ''
      }
      this.refresh()
    },
    confimAddState () {
      this.$refs.statesTemplate.validate((valid) => {
        if (valid) {
          if (this.statesTemplate.value === '' || this.statesTemplate.value === null) {
            this.$message.warning('Please fill in the form correctly!')
            return
          }
          this.stateDialog = false
          if (this.updateFlag) {
            this.states[this.index] = this.statesTemplate
          } else {
            this.states.push(this.statesTemplate)
          }
          this.cancelAddState()
          this.statesTemplate = {
            category: '',
            state: '',
            value: '',
            location: ''
          }
        } else {
          this.$message.warning('Please fill in the form correctly!')
        }
      })
    },
    // 
    refresh () {
      this.$forceUpdate()
    }
  }
}
</script>

<style>
.effect-state-container {
  height: 100%;
  width: 100%;
}

.home-title {
  margin-top: 10px;
  margin-bottom: 10px;
}

.card-margin {
  margin: 10px;
}

.el-card__body {
  width: 90%;
  margin-top: 20px;
  margin: auto auto;
}

.commit-form .el-form-item__content {
  padding-left: 15%;
}

.commit-form .el-form-item__label {
  margin-left: 5%;
}
</style>
