<template>
  <el-container class="effect-time-container">
    <el-main>
      <h1 class="home-title">Effect-Time</h1>
      <div style="display: flex;flex-direction: row-reverse;margin-top: 10px;">
        <el-button type="primary" @click="dialogVisible = true" style="margin-right: 30px;margin-bottom: 10px;">CONFIRM</el-button>
      </div>
      <el-dialog title="CONFIRM PROPERTY" :visible.sync="dialogVisible" center width="50%">
        <el-form ref="strategy" :model="strategy" label-width="20%" :rules="resolve_rule" class="commit-form">
          <el-form-item label="Resolve Strategy" prop="resolve">
            <el-select v-model="strategy.resolve" placeholder="Please Choose the Resolve Strategy">
              <el-option v-for="item in resolveOptions" :key="item.value" :label="item.label" :value="item.value" :title="item.description">
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <span slot="footer">
          <el-button @click="dialogVisible = false">Cancel</el-button>
          <el-button type="primary" @click="Confirm">Confirm</el-button>
        </span>
      </el-dialog>
      <el-alert style="margin-bottom:20px;" title="The execution time of the [Effect] must be [greater than/less than] [Time]." type="success" :closable="false">
      </el-alert>
      <el-form ref="EffectTime" :model="EffectTime" label-width="20%" :rules="effect_time_rule">
        <el-row :gutter='5' type="flex">
          <el-col :span='9'>
            <div class='effect-title' style="font-weight: bold;">
              Effect
            </div>
            <el-form-item label="Location" prop="location">
              <el-select v-model="EffectTime.location" placeholder="Please Choose the Location" @change="LocationChange" style="width: 90%;float:right;margin-right:10px;">
                <el-option v-for="item in locationOptions" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="Effect" prop="effect">
              <el-select v-model="EffectTime.effect" placeholder="Please Choose the Effect" style="width: 90%;float:right;margin-right:10px;">
                <el-option v-for="item in effectsOptions" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span='6' class="desc">
            <div style="font-size:15pt;width:50%">
              <div style="font-weight: bold;">
                Must Execute
              </div>
              <el-form-item label-width="0" prop="symbol" class="symbol">
                <el-select v-model="EffectTime.symbol" placeholder="Symbol" style="width: 90%;margin-right:10px;margin-top:10%;">
                  <el-option v-for="item in symbolOptions" :key="item.value" :label="item.label" :value="item.value">
                  </el-option>
                </el-select>
              </el-form-item>
            </div>
          </el-col>
          <el-col :span='9'>
            <div class='time-title' style="font-weight: bold;">
              Time
            </div>
            <el-form-item label-width="0" prop="time">
              <span>
                <el-input-number v-model="EffectTime.time" controls-position="right" :min="1" style="width: 20%;margin-right:5%">
                </el-input-number>
              </span>
              <el-select v-model="EffectTime.timeUnit" placeholder="Minimum/Maximum Execution Time" style="width: 60%">
                <el-option label="min" value="min"></el-option>
                <el-option label="hour" value="hour"></el-option>
                <el-option label="day" value="day"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
    </el-main>
  </el-container>
</template>

<script>
import { symbolOptions, resolveOptions } from '../../public/global/data.js'
import axios from 'axios'
export default {
  methods: {
    getLocation () {
      const that = this
      this.locationOptions = []
      axios.get('/' + this.URL.url + '/room_list', {
        headers: {
          'Content-Type': 'application/text'
        }
      }).then(res => {
        for (let i = 0; i < res.data.length; i++) {
          that.locationOptions.push({
            value: res.data[i],
            label: res.data[i]
          })
        }
      }).catch(err => {
        console.log(err)
      })
    },
    Confirm () {
      this.$refs.EffectTime.validate((valid) => {
        this.$refs.strategy.validate((strategy) => {
          if (!valid) {
            this.$message.warning('Please fill in the form correctly!')
            this.dialogVisible = false
          } else if (!strategy) {
            this.$message.warning('Please fill in the form correctly!')
          } else {
            if (this.EffectTime.timeUnit === '') {
              this.$message.warning('Please fill in the form correctly!')
              this.dialogVisible = false
              return
            }
            this.EffectTimeDialog = false
            const value = this.EffectTime.location + '.' + this.EffectTime.effect + ' & ' + this.EffectTime.symbol + ' & ' + this.EffectTime.time + this.EffectTime.timeUnit
            if (this.$route.params.update >= 0) {
              this.PROPERTY.property[this.$route.params.update].value = value
              this.PROPERTY.property[this.$route.params.update].strategy = this.strategy.resolve
              this.PROPERTY.property[this.$route.params.update].description = 'The duration of [ ' + this.EffectTime.location + '.' + this.EffectTime.effect + ' ] must [ ' + this.EffectTime.symbol + ' ' + this.EffectTime.time + this.EffectTime.timeUnit + ' ]'
            } else {
              this.PROPERTY.property.push({
                template: 'effect-time',
                value: value,
                strategy: this.strategy.resolve,
                description: 'The duration of [ ' + this.EffectTime.location + '.' + this.EffectTime.effect + ' ] must [ ' + this.EffectTime.symbol + ' ' + this.EffectTime.time + this.EffectTime.timeUnit + ' ]'
              })
            }
            this.$router.back()
          }
        })
      })
    },
    LocationChange (val) {
      this.EffectTime.effect = null
      this.effectsOptions = []
      const that = this
      axios.get('/' + this.URL.url + '/effect_list/' + val, {
        headers: {
          'Content-Type': 'application/text'
        }
      }).then(res => {
        for (let i = 0; i < res.data.length; i++) {
          that.effectsOptions.push({
            value: res.data[i].slice(7, res.data[i].length),
            label: res.data[i].slice(7, res.data[i].length)
          })
        }
      }).catch(err => {
        console.log(err)
      })
    }
  },
  mounted () {
    this.getLocation()
    if (this.$route.params.update >= 0) {
      const temp = this.$route.params.value.split(' & ')
      temp[0] = temp[0].split('.')
      this.LocationChange(temp[0][0])
      this.EffectTime = {
        location: temp[0][0],
        effect: temp[0][1],
        symbol: temp[1],
        time: parseInt(temp[2]),
        timeUnit: (/[^0-9]+/g.exec(temp[2]))[0]
      }
    }
  },
  data () {
    return {
      resolveOptions,
      dialogVisible: false,
      EffectTimeDialog: false,
      symbolOptions,
      locationOptions: [],
      effectsOptions: [],
      EffectTime: {
        effect: '',
        location: '',
        symbol: '',
        time: '',
        timeUnit: ''
      },
      strategy: {
        resolve: ''
      },
      resolve_rule: {
        resolve: [
          { required: true, message: 'please select the resolve strategy', trigger: 'blur' }
        ]
      },
      effect_time_rule: {
        effect: [
          { required: true, message: 'please select the effect.', trigger: 'blur' }
        ],
        location: [
          { required: true, message: 'please select the location.', trigger: 'blur' }
        ],
        symbol: [
          { required: true, message: 'please select the symbol.', trigger: 'blur' }
        ],
        time: [
          { required: true, message: 'please fill the execution time of effect.', trigger: 'blur' }
        ]
      }
    }
  }
}
</script>

<style>
.effect-title,
.time-title {
  margin-bottom: 5%;
  font-size: 20pt;
  padding-top: 8px;
  padding-bottom: 8px;
}

.effect-title {
  background-color: rgb(247, 234, 205);
}

.effect-time-container {
  height: 100%;
  width: 100%;
}

.home-title {
  margin-top: 10px;
  margin-bottom: 10px;
}

.card-margin {
  margin: 10px;
}

.symbol .el-input .el-input__inner {
  font-size: 15pt;
  height: 60px;
  font-weight: bolder;
  padding: 0 4px;
  padding-left: 12%;
}

.symbol .el-select,
.el-input,
.el-input__inner {
  border: 0px;
  border-radius: 0px;
}

.el-card__body {
  width: 90%;
  margin-top: 20px;
  margin: auto auto;
}

.commit-form .el-form-item__label {
  margin-left: 5%;
}
</style>
