<template>
  <el-container class="event-state-time-container">
    <el-main style="padding: 20px 15px">
      <h1 class="title">Event-State-Time</h1>
      <div style="display: flex;flex-direction: row-reverse;margin-top: 15px;">
        <el-button type="primary" @click="dialogVisible = true" style="margin-right: 30px;margin-bottom: 8px;">CONFIRM</el-button>
      </div>
      <el-dialog title="CONFIRM PROPERTY" :visible.sync="dialogVisible" center width="50%">
        <el-form ref="strategy" :model="strategy" label-width="20%" :rules="resolve_rule" class="commit-form">
          <el-form-item label="Resolve Strategy" prop="resolve">
            <el-select v-model="strategy.resolve" placeholder="Please Choose the Resolve Strategy">
              <el-option v-for="item in resolveOptions" :key="item.value" :label="item.label" :value="item.value" :title="item.description">
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <span slot="footer">
          <el-button @click="dialogVisible = false">Cancel</el-button>
          <el-button type="primary" @click="Confirm">Confirm</el-button>
        </span>
      </el-dialog>
      <el-alert style="margin-top:10px;margin-bottom:10px;" v-if="radio == 'Always'" title="The [State] always occur within the [Time] of the [Event] occurrence." type="success" :closable="false">
      </el-alert>
      <el-alert style="margin-top:10px;margin-bottom:10px;" v-if="radio == 'Never'" title="The [State] never occur within the [Time] of the [Event] occurrence." type="success" :closable="false">
      </el-alert>
      <el-row :gutter='5' type="flex">
        <el-col :span='7'>
          <div class='state-title' style="font-weight: bold;">
            State
          </div>
          <div v-for="(t, i) in states" :key="i">
            <el-card class="card-margin">
              <el-descriptions :title="'State ' + (i + 1)" :column="1" border>
                <template slot="extra">
                  <el-button size="mini" icon="el-icon-edit" @click="updateItem(t, i)">
                  </el-button>
                  <el-button size="mini" icon="el-icon-delete" @click="removeItem(i)"></el-button>
                </template>
                <el-descriptions-item label="location">{{ t.location}}</el-descriptions-item>
                <el-descriptions-item label="state">{{ t.category + '.' + t.state  + '.' + t.value}}</el-descriptions-item>
              </el-descriptions>
            </el-card>
          </div>
          <el-card style="display: flex;align-content: center;justify-content: center;" class="card-margin">
            <el-button type="text" @click="addState()">
              <i class="el-icon-circle-plus" style="font-size: 25px;"> Add State</i>
            </el-button>
            <el-dialog title="State" :visible.sync="stateDialog" @close="cancelAddState">
              <el-form ref="statesTemplate" :model="statesTemplate" label-width="20%" :rules="state_rules">
                <el-form-item label="Location" prop="location">
                  <el-select v-model="statesTemplate.location" placeholder="Please Choose the Location" @change="statesLocationChange">
                    <el-option v-for="item in locationOptions" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="category" prop="category">
                  <el-select v-model="statesTemplate.category" placeholder="Please Choose the Category" @change="statesCategoryChange">
                    <el-option label="device" value="device"></el-option>
                    <el-option label="state" value="state"></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="state" prop="state">
                  <el-select v-if="statesTemplate.category=='device'" v-model="statesTemplate.state" style="width: 120px;margin-right:20px" placeholder="Please Choose the Device"
                    @change="statesChange">
                    <el-option v-for="item in deviceOptions" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                  </el-select>
                  <el-select v-if="statesTemplate.category=='state'" v-model="statesTemplate.state" style="width: 120px;margin-right:20px" placeholder="Please Choose the State" @change="statesChange">
                    <el-option v-for="item in stateOptions" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                  </el-select>
                  <el-select v-model="statesTemplate.value" placeholder="Please Choose the Value">
                    <el-option v-for="item in valuesOptions" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                  </el-select>
                </el-form-item>
              </el-form>
              <div slot="footer">
                <el-button @click="cancelAddState">Cancel</el-button>
                <el-button type="primary" @click="confimAddState">Confirm</el-button>
              </div>
            </el-dialog>
          </el-card>
        </el-col>
        <el-col :span='3' class="desc">
          <div style="font-size:15pt;width:99%">
            <el-select v-model="radio" style="width: 70%;" placeholder="Frequency" class="radio">
              <el-option v-for="item in radioOptions" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
            <div style="margin-top:12%;font-weight: bold;">
              occur within
            </div>
          </div>
        </el-col>
        <el-col :span='6'>
          <div class='time-title' style="font-weight: bold;">
            Time
          </div>
          <el-form ref="timeTemplate" :model="timeTemplate" :rules="time_rules">
            <el-form-item prop="time">
              <span>
                <el-input-number v-model="timeTemplate.time" controls-position="right" :min="1" style="width: 25%;margin-right:20px">
                </el-input-number>
              </span>
              <el-select v-model="timeTemplate.timeUnit" placeholder="Please Choose the Minimum Interval" style="width: 60%">
                <el-option label="min" value="min"></el-option>
                <el-option label="hour" value="hour"></el-option>
                <el-option label="day" value="day"></el-option>
              </el-select>
            </el-form-item>
          </el-form>
        </el-col>
        <el-col :span='2' class="desc" style="font-weight: bold;font-size:15pt;">after
        </el-col>
        <el-col :span='6'>
          <div class='event-title' style="font-weight: bold;">
            Event
          </div>
          <el-form ref="eventTemplate" :model="eventTemplate" :rules="event_rules">
            <el-form-item label="Location" prop="location">
              <el-select v-model="eventTemplate.location" placeholder="Please Choose the Location of the Event" @change="eventLocationChange" style="width: 70%;float:right;margin-right:10px;">
                <el-option v-for="item in locationOptions" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="Event" prop="event">
              <el-select v-model="eventTemplate.type" placeholder="Please Choose the Type" @change="typeChange" style="width: 70%;float:right;margin-bottom:5px;margin-right:10px;">
                <el-option v-for="item in typeOptions" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
              <el-select v-model="eventTemplate.event" placeholder="Please Choose the Event" v-if="eventTemplate.type == 'event'" style="width: 70%;float:right;margin-right:10px;">
                <el-option v-for="item in eventOptions" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
              <el-select v-model="eventTemplate.event" placeholder="Please Choose the Action" v-if="eventTemplate.type == 'action'" style="width: 70%;float:right;margin-right:10px;">
                <el-option v-for="item in actionOptions" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-form>
        </el-col>
      </el-row>
    </el-main>
  </el-container>
</template>

<script>
import { symbolOptions, typeOptions, resolveOptions, radioOptions } from '../../public/global/data.js'
import axios from 'axios'
export default {
  methods: {
    refresh () {
      this.$forceUpdate()
    },
    addState () {
      this.stateDialog = true
      this.updateFlag = false
    },
    updateItem (Info, index) {
      if (Info && index >= 0) {
        const item = JSON.parse(JSON.stringify(Info))
        this.stateDialog = true
        this.statesTemplate = item
        this.inquiryInfor(this.statesTemplate.category)
        this.statesChange(this.statesTemplate.state)
        this.index = index
        this.updateFlag = true
      }
    },
    removeItem (index) {
      this.states.splice(index, 1)
      this.refresh()
    },
    statesChange (val) {
      this.statesTemplate.value = null
      if (this.statesTemplate.state) {
        this.valuesOptions = this.STATE.state[val]
      } else {
        this.valuesOptions = []
      }
    },
    cancelAddState () {
      this.stateDialog = false
      this.refresh()
    },
    confimAddState () {
      this.$refs.statesTemplate.validate((valid) => {
        if (valid) {
          if (this.statesTemplate.value === '' || this.statesTemplate.value === null) {
            this.$message.warning('Please fill in the form correctly!')
            return
          }
          this.stateDialog = false
          if (this.updateFlag) {
            this.states[this.index] = this.statesTemplate
          } else {
            this.states.push(this.statesTemplate)
          }
          this.cancelAddState()
          this.statesTemplate = {
            category: '',
            state: '',
            value: ''
          }
        } else {
          this.$message.warning('Please fill in the form correctly!')
        }
      })
    },
    eventLocationChange (val) {
      this.eventTemplate.event = null
      const that = this
      axios.get('/' + this.URL.url + '/event_list/' + val, {
        headers: {
          'Content-Type': 'application/text'
        }
      }).then(res => {
        this.eventOptions = []
        for (let i = 0; i < res.data.length; i++) {
          that.eventOptions.push({
            value: res.data[i].slice(6, res.data[i].length),
            label: res.data[i].slice(6, res.data[i].length)
          })
        }
      }).catch(err => {
        console.log(err)
      })
      axios.get('/' + this.URL.url + '/room_action/' + val, {
        headers: {
          'Content-Type': 'application/text'
        }
      }).then(res => {
        this.actionOptions = []
        for (let i = 0; i < res.data.length; i++) {
          that.actionOptions.push({
            value: res.data[i],
            label: res.data[i]
          })
        }
      }).catch(err => {
        console.log(err)
      })
    },
    typeChange (val) {
      this.eventTemplate.event = null
    },
    getLocation () {
      const that = this
      axios.get('/' + this.URL.url + '/room_list', {
        headers: {
          'Content-Type': 'application/text'
        }
      }).then(res => {
        for (let i = 0; i < res.data.length; i++) {
          that.locationOptions.push({
            value: res.data[i],
            label: res.data[i]
          })
        }
      }).catch(err => {
        console.log(err)
      })
    },
    statesLocationChange (val) {
      this.statesTemplate.state = null
      this.statesTemplate.value = null
      this.inquiryInfor(this.statesTemplate.category)
    },
    statesCategoryChange (val) {
      this.statesTemplate.state = null
      this.statesTemplate.value = null
      this.inquiryInfor(val)
    },
    inquiryInfor (val) {
      const that = this
      if (val === 'device') {
        this.deviceOptions = []
        axios.get('/' + this.URL.url + '/room_device/' + that.statesTemplate.location, {
          headers: {
            'Content-Type': 'application/text'
          }
        }).then(res => {
          for (let i = 0; i < res.data.length; i++) {
            const data = res.data[i].slice(0, -3)
            // var temp = data[0:-3]
            that.deviceOptions.push({
              value: data,
              label: data
            })
          }
        }).catch(err => {
          console.log(err)
        })
      } else if (val === 'state') {
        this.stateOptions = []
        axios.get('/' + this.URL.url + '/room_state/' + that.statesTemplate.location, {
          headers: {
            'Content-Type': 'application/text'
          }
        }).then(res => {
          for (let i = 0; i < res.data.length; i++) {
            that.stateOptions.push({
              value: res.data[i],
              label: res.data[i]
            })
          }
        }).catch(err => {
          console.log(err)
        })
      }
    },
    Confirm () {
      this.$refs.eventTemplate.validate((event) => {
        this.$refs.timeTemplate.validate((time) => {
          this.$refs.strategy.validate((strategy) => {
            if (!event || !time) {
              this.$message.warning('Please fill in the form correctly!')
              this.dialogVisible = false
            } else if (!strategy) {
              this.$message.warning('Please fill in the form correctly!')
            } else {
              if (this.timeTemplate.timeUnit === '' || this.eventTemplate.event === '' || this.eventTemplate.event === null || this.eventTemplate.event === undefined) {
                this.$message.warning('Please fill in the form correctly!')
                this.dialogVisible = false
                return
              }
              if (this.states.length < 1) {
                this.$message.warning('Please fill in at least one state!')
                this.dialogVisible = false
                return
              }
              let stateList = this.states[0].location + '.' + this.states[0].state + '.' + this.states[0].value
              let value = this.eventTemplate.location + '.' + this.eventTemplate.type + '.' + this.eventTemplate.event + ' & ' + '[ ' + this.states[0].location + '.' + this.states[0].category + '.' + this.states[0].state + '.' + this.states[0].value
              let descList = this.states[0].location + '.' + this.states[0].state + '.' + this.states[0].value
              for (let i = 1; i < this.states.length; i++) {
                value = value + ' & ' + this.states[i].location + '.' + this.states[i].category + '.' + this.states[i].state + '.' + this.states[i].value
                descList = descList + ' , ' + this.states[i].location + '.' + this.states[i].state + '.' + this.states[i].value
                stateList = stateList + ' & ' + this.states[i].location + '.' + this.states[i].state + '.' + this.states[i].value
              }
              value = value + ' ]' + ' & ' + this.radio + ' & ' + this.timeTemplate.time + this.timeTemplate.timeUnit
              if (this.$route.params.update >= 0) {
                this.PROPERTY.property[this.$route.params.update].value = value
                this.PROPERTY.property[this.$route.params.update].strategy = this.strategy.resolve
                this.PROPERTY.property[this.$route.params.update].description = 'After [' + descList + '], within  [ ' + this.timeTemplate.time + this.timeTemplate.timeUnit + ' ], [ ' + this.eventTemplate.location + '.' + this.eventTemplate.event + ' ] will [ ' + this.radio + ' ] happen.'
              } else {
                this.PROPERTY.property.push(
                  {
                    template: 'event-state-time',
                    value: value,
                    strategy: this.strategy.resolve,
                    description: 'After [' + this.eventTemplate.location + '.' + this.eventTemplate.event + '], within  [ ' + this.timeTemplate.time + this.timeTemplate.timeUnit + ' ], [ ' + descList + ' ] will [ ' + this.radio + ' ] happen.'
                  }
                )
              }
              this.$router.back()
            }
          })
        })
      })
    }
  },
  mounted () {
    this.getLocation()
    if (this.$route.params.update >= 0) {
      const temp = this.$route.params.value.split(' & ')
      temp[1] = temp[1].substring(2)
      temp[temp.length - 3] = temp[temp.length - 3].substring(0, temp[temp.length - 3].length - 2)
      const event = temp[0].split('.')
      this.radio = temp[temp.length - 2]
      this.eventLocationChange(event[0])
      this.eventTemplate = {
        location: event[0],
        type: event[1],
        event: event[2] + '.' + event[3]
      }
      this.timeTemplate = {
        time: parseInt(temp[temp.length - 1]),
        timeUnit: (/[^0-9]+/g.exec(temp[temp.length - 1]))[0]
      }
      for (let i = 1; i < temp.length - 2; i++) {
        const item = temp[i].split('.')
        this.states.push({
          location: item[0],
          category: item[1],
          state: item[2],
          value: item[3]
        })
      }
    }
  },
  data () {
    return {
      stateDialog: false,
      radio: 'Always',
      resolveOptions,
      radioOptions,
      locationOptions: [],
      eventOptions: [],
      eventOptionsTwo: [],
      valuesOptions: [],
      actionOptions: [],
      stateOptions: [],
      deviceOptions: [],
      typeOptions,
      symbolOptions,
      dialogVisible: false,
      EventStateTimeDialog: false,
      eventTemplate: {
        location: '',
        type: '',
        event: ''
      },
      timeTemplate: {
        time: '',
        timeUnit: ''
      },
      state: {
        category: '',
        state: '',
        location: '',
        value: ''
      },
      statesTemplate: {
        category: '',
        state: '',
        location: '',
        value: ''
      },
      states: [],
      state_rules: {
        location: [
          { required: true, message: 'Please select the location.', trigger: 'blur' }
        ],
        type: [
          { required: true, message: 'please select the type.', trigger: 'blur' }
        ],
        event: [
          { required: true, message: 'Please select the event.', trigger: 'blur' }
        ]
      },
      event_rules: {
        location: [
          { required: true, message: 'Please select the location.', trigger: 'blur' }
        ],
        category: [
          { required: true, message: 'please select the category.', trigger: 'blur' }
        ],
        state: [
          { required: true, message: 'Please select the state.', trigger: 'blur' }
        ]
      },
      time_rules: {
        time: [
          { required: true, message: 'please select the min time time between event and state.', trigger: 'blur' }
        ],
        timeUnit: [
          { required: true, message: 'please fill the execution timeUnit of event.', trigger: 'blur' }
        ]
      },
      strategy: {
        resolve: ''
      },
      resolve_rule: {
        resolve: [
          { required: true, message: 'please select the resolve strategy', trigger: 'blur' }
        ]
      }
    }
  }
}
</script>

<style>
.event-state-time-container {
  height: 100%;
  width: 100%;
}

.card-margin {
  margin: 10px;
}

.radio .el-input .el-input__inner {
  font-size: 15pt;
  height: 60px;
  font-weight: bolder;
  padding: 0 4px;
  padding-left: 10%;
}

.radio .el-select,
.el-input,
.el-input__inner {
  border: 0px;
  border-radius: 0px;
}

.el-select .el-input.is-focus .el-input__inner {
  border: 0px;
}

.el-card__body {
  padding: 10px 0px !important;
  margin-top: 20px;
  margin: auto auto;
}

.state-title,
.time-title,
.event-title {
  margin-bottom: 5%;
  font-size: 20pt;
  padding-top: 8px;
  padding-bottom: 8px;
}

.state-title {
  background-color: rgb(221, 252, 204);
}

.time-title {
  background-color: rgb(242, 244, 199);
}

.event-title {
  background-color: rgb(187, 231, 232);
}

.desc {
  display: flex;
  justify-content: center;
  align-items: center;
}

.title {
  font-size: 23pt;
  margin-top: 0;
  margin-bottom: 0;
}

.el-form-item {
  display: inline-block !important;
  width: 100%;
}

.el-avatar,
.el-cascader-panel,
.el-radio,
.el-radio--medium.is-bordered .el-radio__label,
.el-radio__label {
  font-size: 15pt !important;
  margin: 15px 5px !important;
}

.el-form-item__label {
  width: 20%;
}
</style>
