<template>
  <el-container class="state-state-container">
    <el-main>
      <h1 class="home-title">State-State-Condition</h1>
      <div style="display: flex;flex-direction: row-reverse;margin-top: 20px;">
        <el-button type="primary" @click="dialogVisible = true" style="margin-right: 30px;margin-bottom: 10px;">CONFIRM</el-button>
      </div>
      <el-alert style="margin-top:10px;margin-bottom:20px;" v-if="radio == 'Always'" title="[state1, ..., staten] should always be active at the same time." type="success" :closable="false">
      </el-alert>
      <el-alert style="margin-top:10px;margin-bottom:20px;" v-if="radio == 'Never'" title="[state1, ..., staten] should never be active at the same time" type="success" :closable="false">
      </el-alert>
      <el-dialog title="CONFIRM PROPERTY" :visible.sync="dialogVisible" center width="50%">
        <el-form ref="strategy" :model="strategy" label-width="20%" :rules="resolve_rule" class="commit-form">
          <el-form-item label="Resolve Strategy" prop="resolve">
            <el-select v-model="strategy.resolve" placeholder="Please Choose the Resolve Strategy">
              <el-option v-for="item in resolveOptions" :key="item.value" :label="item.label" :value="item.value" :title="item.description">
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <span slot="footer">
          <el-button @click="dialogVisible = false">Cancel</el-button>
          <el-button type="primary" @click="Confirm">Confirm</el-button>
        </span>
      </el-dialog>

      <el-row :gutter='5' type="flex">
        <el-col :span='13'>
          <!-- list -->
          <div v-for="(t, i) in states" :key="i">
            <el-card class="card-margin">
              <el-descriptions :title="'State ' + (i + 1)" :column="1" border>
                <template slot="extra">
                  <el-button size="mini" icon="el-icon-edit" @click="updateItem(t, i)">
                  </el-button>
                  <el-button size="mini" icon="el-icon-delete" @click="removeItem(i)"></el-button>
                </template>
                <el-descriptions-item label="location">{{ t.location}}</el-descriptions-item>
                <el-descriptions-item label="state">{{ t.category + '.' + t.state  + '.' + t.value}}</el-descriptions-item>
              </el-descriptions>
            </el-card>
          </div>
          <!--  -->
          <el-card style="display: flex;align-content: center;justify-content: center;" class="card-margin">
            <el-button type="text" @click="addState()">
              <i class="el-icon-circle-plus" style="font-size: 25px;"> Add State</i>
            </el-button>
            <el-dialog title="State" :visible.sync="stateDialog" @close="cancelAddState">
              <el-form ref="statesTemplate" :model="statesTemplate" label-width="20%" :rules="state_rules">
                <el-form-item label="Location" prop="location">
                  <el-select v-model="statesTemplate.location" placeholder="Please Choose the Location" @change="statesLocationChange">
                    <el-option v-for="item in locationOptions" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="category" prop="category">
                  <el-select v-model="statesTemplate.category" placeholder="Please Choose the Category" @change="statesCategoryChange">
                    <el-option label="device" value="device"></el-option>
                    <el-option label="state" value="state"></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="state" prop="state">
                  <el-select v-if="statesTemplate.category=='device'" v-model="statesTemplate.state" style="width: 120px;margin-right:20px" placeholder="Please Choose the Device"
                    @change="statesChange">
                    <el-option v-for="item in devicesOptions" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                  </el-select>
                  <el-select v-if="statesTemplate.category=='state'" v-model="statesTemplate.state" style="width: 120px;margin-right:20px" placeholder="Please Choose the State" @change="statesChange">
                    <el-option v-for="item in statesOptions" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                  </el-select>
                  <el-select v-model="statesTemplate.value" placeholder="Please Choose the Value">
                    <el-option v-for="item in valuesOptions" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                  </el-select>
                </el-form-item>
              </el-form>
              <div slot="footer">
                <el-button @click="cancelAddState">Cancel</el-button>
                <el-button type="primary" @click="confimAddState">Confirm</el-button>
              </div>
            </el-dialog>
          </el-card>
        </el-col>
        <el-col :span='5' class="desc">
          <div style="font-size:15pt;width:99%">
            <el-select v-model="radio" style="width: 70%;" placeholder="Frequency" class="radio">
              <el-option v-for="item in radioOptions" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </div>
        </el-col>
        <el-col :span='6' class="desc">
          <div style="font-size:15pt;font-weight: bold;">
            be active at the same time
          </div>
        </el-col>
      </el-row>
    </el-main>
  </el-container>
</template>

<script>
import axios from 'axios'
import { resolveOptions, radioOptions } from '../../public/global/data.js'
export default {
  name: 'StateState',
  components: {
  },
  data () {
    return {
      resolveOptions,
      radioOptions,
      dialogVisible: false,
      stateDialog: false,
      states: [],
      statesTemplate: {
        category: '',
        state: '',
        location: '',
        value: ''
      },
      state_rules: {
        location: [
          { required: true, message: 'Please select the location.', trigger: 'blur' }
        ],
        category: [
          { required: true, message: 'please select the category.', trigger: 'blur' }
        ],
        state: [
          { required: true, message: 'Please select the state.', trigger: 'blur' }
        ]
      },
      strategy: {
        resolve: ''
      },
      resolve_rule: {
        resolve: [
          { required: true, message: 'please select the resolve strategy', trigger: 'blur' }
        ]
      },
      radio: 'Always',
      locationOptions: [],
      statesOptions: [],
      devicesOptions: [],
      valuesOptions: [],
      index: '',
      updateFlag: false
    }
  },
  mounted () {
    this.getLocation()
    if (this.$route.params.update >= 0) {
      const temp = this.$route.params.value.split(' & ')
      this.radio = temp[2]
      for (let i = 0; i < temp.length - 1; i++) {
        const item = temp[i].split('.')
        this.states.push({
          location: item[0],
          category: item[1],
          state: item[2],
          value: item[3]
        })
      }
    }
  },
  methods: {
    updateItem (Info, index) {
      if (Info && index >= 0) {
        const item = JSON.parse(JSON.stringify(Info))
        this.stateDialog = true
        this.statesTemplate = item
        this.statesChange(this.statesTemplate.state)
        this.inquiryInfor(this.statesTemplate.category, 'statesTemplate')
        this.index = index
        this.updateFlag = true
      }
    },
    removeItem (index) {
      this.states.splice(index, 1)
      this.refresh()
    },
    getLocation () {
      const that = this
      this.locationOptions = []
      axios.get('/' + this.URL.url + '/room_list', {
        headers: {
          'Content-Type': 'application/text'
        }
      }).then(res => {
        for (let i = 0; i < res.data.length; i++) {
          that.locationOptions.push({
            value: res.data[i],
            label: res.data[i]
          })
        }
      }).catch(err => {
        console.log(err)
      })
    },
    Confirm () {
      if (this.states.length < 2) {
        this.$message.warning('Please select more than two effects!')
        this.dialogVisible = false
        return
      }
      this.$refs.strategy.validate((strategy) => {
        if (strategy) {
          let value = this.states[0].location + '.' + this.states[0].category + '.' + this.states[0].state + '.' + this.states[0].value
          let tempList = this.states[0].location + '.' + this.states[0].state + '.' + this.states[0].value
          for (let i = 1; i < this.states.length; i++) {
            value = value + ' & ' + this.states[i].location + '.' + this.states[i].category + '.' + this.states[i].state + '.' + this.states[i].value
            tempList = tempList + ' , ' + this.states[i].location + '.' + this.states[i].state + '.' + this.states[i].value
          }
          value = value + ' & ' + this.radio
          let ltl = ''
          if (this.radio === 'Always') {
            ltl = 'G(' + tempList + ')'
          } else {
            ltl = 'F(' + tempList + ')'
          }
          if (this.$route.params.update >= 0) {
            this.PROPERTY.property[this.$route.params.update].value = value
            this.PROPERTY.property[this.$route.params.update].strategy = this.strategy.resolve
            this.PROPERTY.property[this.$route.params.update].description = '[ ' + tempList + ' ] will [ ' + this.radio + ' ] happen simultaneously.'
            this.PROPERTY.property[this.$route.params.update].ltl = ltl
          } else {
            this.PROPERTY.property.push(
              {
                template: 'state-state',
                value: value,
                strategy: this.strategy.resolve,
                description: '[ ' + tempList + ' ] will [ ' + this.radio + ' ] happen simultaneously.',
                ltl: ltl
              }
            )
          }
          this.dialogVisible = false
          this.$router.back()
        } else {
          this.$message.warning('Please fill in the form correctly!')
        }
      })
    },
    inquiryInfor (val, template) {
      const that = this
      if (template === 'state') {
        if (val === 'device') {
          this.deviceOptions = []
          axios.get('/' + this.URL.url + '/room_device/' + that.state.location, {
            headers: {
              'Content-Type': 'application/text'
            }
          }).then(res => {
            for (let i = 0; i < res.data.length; i++) {
              const data = res.data[i].slice(0, -3)
              // var temp = data[0:-3]
              that.deviceOptions.push({
                value: data,
                label: data
              })
            }
          }).catch(err => {
            console.log(err)
          })
        } else if (val === 'state') {
          this.stateOptions = []
          axios.get('/' + this.URL.url + '/room_state/' + that.state.location, {
            headers: {
              'Content-Type': 'application/text'
            }
          }).then(res => {
            for (let i = 0; i < res.data.length; i++) {
              that.stateOptions.push({
                value: res.data[i],
                label: res.data[i]
              })
            }
          }).catch(err => {
            console.log(err)
          })
        }
      } else if (template === 'statesTemplate') {
        const that = this
        if (this.statesTemplate.category === 'device') {
          this.devicesOptions = []
          axios.get('/' + this.URL.url + '/room_device/' + that.statesTemplate.location, {
            headers: {
              'Content-Type': 'application/text'
            }
          }).then(res => {
            for (let i = 0; i < res.data.length; i++) {
              const data = res.data[i].slice(0, -3)
              // var temp = data[0:-3]
              that.devicesOptions.push({
                value: data,
                label: data
              })
            }
          }).catch(err => {
            console.log(err)
          })
        } else if (this.statesTemplate.category === 'state') {
          this.statesOptions = []
          axios.get('/' + this.URL.url + '/room_state/' + that.statesTemplate.location, {
            headers: {
              'Content-Type': 'application/text'
            }
          }).then(res => {
            for (let i = 0; i < res.data.length; i++) {
              that.statesOptions.push({
                value: res.data[i],
                label: res.data[i]
              })
            }
          }).catch(err => {
            console.log(err)
          })
        }
      }
    },
    statesLocationChange (val) {
      this.statesTemplate.state = null
      this.statesTemplate.value = null
      this.inquiryInfor(this.statesTemplate.category, 'statesTemplate')
    },
    statesCategoryChange (val) {
      this.statesTemplate.state = null
      this.statesTemplate.value = null
      this.inquiryInfor(val, 'statesTemplate')
    },
    statesChange (val) {
      this.statesTemplate.value = null
      if (this.statesTemplate.state) {
        this.valuesOptions = this.STATE.state[val]
      } else {
        this.valuesOptions = []
      }
    },
    addState () {
      this.stateDialog = true
      this.updateFlag = false
    },
    cancelAddState () {
      this.stateDialog = false
      this.refresh()
    },
    confimAddState () {
      this.$refs.statesTemplate.validate((valid) => {
        if (valid) {
          if (this.statesTemplate.value === '' || this.statesTemplate.value === null) {
            this.$message.warning('Please fill in the form correctly!')
            return
          }
          this.stateDialog = false
          if (this.updateFlag) {
            this.states[this.index] = this.statesTemplate
          } else {
            this.states.push(this.statesTemplate)
          }
          this.cancelAddState()
          this.statesTemplate = {
            category: '',
            state: '',
            value: ''
          }
        } else {
          this.$message.warning('Please fill in the form correctly!')
        }
      })
    },
    // 
    refresh () {
      this.$forceUpdate()
    }
  }
}
</script>

<style>
.state-state-container {
  height: 100%;
  width: 100%;
}

.home-title {
  margin-top: 10px;
  margin-bottom: 10px;
}

.card-margin {
  margin: 10px;
}

.el-card__body {
  width: 90%;
  margin-top: 20px;
  margin: auto auto;
}

.commit-form .el-form-item__content {
  padding-left: 15%;
}

.commit-form .el-form-item__label {
  margin-left: 5%;
}
</style>
