<template>
  <el-container class="container">
    <el-dialog title="CHOOSE ENVIRONMRNT" top="10%" width="40%" :visible.sync="enviromentDialogVisible" :showClose="false" :close-on-click-modal="false">
      <el-row>
        <el-col :span='12'>
          <el-row style="display: flex;justify-content: center;">
            <el-button plain round style="padding:30px;margin-bottom:20px;font-size:16pt;color:white;background-color:rgb(100, 195, 219)" @click="chooseEnvironment('Smart Office')">
              Smart Office
            </el-button>
          </el-row>
        </el-col>
        <el-col :span='12'>
          <el-row style="display: flex;justify-content: center;">
            <el-button plain round style="padding:30px;margin-bottom:20px;font-size:16pt;color:white;background-color:rgb(242, 185, 80)" @click="chooseEnvironment('Smart Home')">
              Smart Home
            </el-button>
          </el-row>
        </el-col>
      </el-row>

      <!-- <el-form ref="enviroment" :model="enviroment" :rules="enviromentRules">
        <el-form-item label="enviroment name" prop="enviroment">
          <el-select v-model="enviroment.enviroment" placeholder="enviroment name" filterable>
            <el-option v-for="item in enviromentOptions" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <el-button type="primary" @click="chooseEnvironment">Confirm</el-button>
      </span> -->
    </el-dialog>
    <el-main>
      <h1 class="home-title">Property Description</h1>
      <div style="display: flex;flex-direction: row-reverse;">
        <el-button type="primary" @click="dialogVisible = true" style="margin-right: 30px;margin-bottom: 10px;">UPLOAD</el-button>
      </div>
      <el-dialog title="UPLOAD CONFIGURATION" :visible.sync="dialogVisible">
        <el-form ref="form" :model="form" :rules="rules">
          <el-form-item label="user name" prop="user">
            <el-input v-model="form.user" placeholder="user name"></el-input>
          </el-form-item>
          <el-form-item label="rule name" prop="rule">
            <el-select v-model="form.rule" placeholder="rule name" filterable>
              <el-option v-for="item in appOptions" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <span slot="footer">
          <el-button @click="dialogVisible = false">Cancel</el-button>
          <el-button type="primary" @click="upload">Confirm</el-button>
        </span>
      </el-dialog>
      <!-- list -->
      <div v-for="(t, i) in propertyList" :key="i">
        <el-card class="card-margin">
          <el-descriptions :title="'Property ' + (i + 1)" :column="1" border>
            <template slot="extra">
              <el-button size="mini" icon="el-icon-edit" @click="updateItem(t, i)">
              </el-button>
              <el-button size="mini" icon="el-icon-delete" @click="removeItem(i)"></el-button>
            </template>
            <!-- <el-descriptions-item label="Abstract Level">{{ t.template }}</el-descriptions-item> -->
            <el-descriptions-item label="Description">{{ t.description }}</el-descriptions-item>
            <el-descriptions-item label="Resolve Strategy">{{ t.strategy }}</el-descriptions-item>
            <!-- <el-descriptions-item label="ltl" v-if="t.ltl">{{ t.ltl }}</el-descriptions-item> -->
          </el-descriptions>
        </el-card>
      </div>
      <!--  -->
      <el-card style="display: flex;align-content: center;justify-content: center;" class="card-margin">
        <el-button type="text" @click="addItem()">
          <i class="el-icon-circle-plus" style="font-size: 25px;"> Add Property</i>
        </el-button>
        <el-dialog center width="85%" :visible.sync="templateDialog" @close="cancelAddItem">
          <el-row>
            <el-col :span='12'>
              <div style="border-radius: 10px;border: 2px solid rgb(130, 160, 180);padding:30px 10px;margin-right:5px">
                <el-row style="display: flex;justify-content: center;">
                  <div class="space-title">Spatial State</div>
                </el-row>
                <el-row style="display: flex;justify-content: center;">
                  <div class="template-spatial" @click="confimAddItem('effect-effect')">
                    Multiple
                    [<span style="color:rgb(43, 195, 219);font-size:15pt;">Effect</span>] should [<span style="color:rgb(43, 195, 219);font-size:15pt;">Always/Never</span>] occur at the same time.
                  </div>
                </el-row>
                <el-row style="display: flex;justify-content: center;">
                  <div class="template-spatial" @click="confimAddItem('state-state')">
                    Multiple
                    [<span style="color:rgb(43, 195, 219);font-size:15pt;">State</span>] should [<span style="color:rgb(43, 195, 219);font-size:15pt;">Always/Never</span>] be active at the same time.
                  </div>
                </el-row>
                <el-row style="display: flex;justify-content: center;">
                  <div class="template-spatial" @click="confimAddItem('event-state')">[<span style="color:rgb(43, 195, 219);font-size:15pt;">Event</span>] should [<span
                      style="color:rgb(43, 195, 219);font-size:15pt;">Always/Never</span>] happen while [<span style="color:rgb(43, 195, 219);font-size:15pt;">State1, ..., Staten</span>].</div>
                </el-row>
                <el-row style="display: flex;justify-content: center;">
                  <div class="template-spatial" @click="confimAddItem('effect-state')">[<span style="color:rgb(43, 195, 219);font-size:15pt;">Effect</span>] should [<span
                      style="color:rgb(43, 195, 219);font-size:15pt;">Always/Never</span>] happen while [<span style="color:rgb(43, 195, 219);font-size:15pt;">State1, ..., Staten</span>].</div>
                </el-row>
              </div>
            </el-col>
            <el-col :span='12'>
              <div style="border-radius: 10px;border: 2px solid rgb(160, 180, 160);padding:30px 10px;margin-left:5px">
                <el-row style="display: flex;justify-content: center;">
                  <div class="time-title">Temporal Trace</div>
                </el-row>
                <el-row style="display: flex;justify-content: center;">
                  <div class="template-temporal" @click="confimAddItem('event-state-time')">The
                    [<span style="color:rgb(152, 210, 98);font-size:15pt;">State</span>] should [<span style="color:rgb(152, 210, 98);font-size:15pt;">Always/Never</span>] occur within the [<span
                      style="color:rgb(152, 210, 98);font-size:15pt;">Time</span>] of the
                    [<span style="color:rgb(152, 210, 98);font-size:15pt;">Event</span>] occurrence.
                  </div>
                </el-row>
                <el-row style="display: flex;justify-content: center;">
                  <div class="template-temporal" @click="confimAddItem('effect-state-time')">The
                    [<span style="color:rgb(152, 210, 98);font-size:15pt;">Effect</span>] should [<span style="color:rgb(152, 210, 98);font-size:15pt;">Always/Never</span>] happen within the [<span
                      style="color:rgb(152, 210, 98);font-size:15pt;">Time</span>]
                    while [<span style="color:rgb(152, 210, 98);font-size:15pt;">State1, ..., Staten</span>].
                  </div>
                </el-row>
                <el-row style="display: flex;justify-content: center;">
                  <div class="template-temporal" @click="confimAddItem('effect-time')">The execution time of the [<span style="color:rgb(152, 210, 98);font-size:15pt;">Effect</span>] must be [<span
                      style="color:rgb(152, 210, 98);font-size:15pt;">greater than/less than</span>] [<span style="color:rgb(152, 210, 98);font-size:15pt;">Time</span>].
                  </div>
                </el-row>
                <el-row style="display: flex;justify-content: center;">
                  <div class="template-temporal" @click="confimAddItem('state-time')">The duration of the [<span style="color:rgb(152, 210, 98);font-size:15pt;">State</span>] must be [<span
                      style="color:rgb(152, 210, 98);font-size:15pt;">greater than/less than</span>] [<span style="color:rgb(152, 210, 98);font-size:15pt;">Time</span>].
                  </div>
                </el-row>
              </div>
            </el-col>
          </el-row>
        </el-dialog>
      </el-card>
    </el-main>
  </el-container>
</template>

<script>
import { appOptions, categoryOptions, symbolOptions, enviromentOptions } from '../../public/global/data.js'
import axios from 'axios'

export default {
  name: 'HomeView',
  components: {
  },
  data () {
    return {
      enviroment: {
        enviroment: ''
      },
      beginFlag: false,
      propertyList: this.PROPERTY.property,
      form: {
        user: '',
        rule: ''
      },
      rules: {
        user: [
          { required: true, message: 'please input your name.', trigger: 'blur' }
        ],
        rule: [
          { required: true, message: 'Please select the application name.', trigger: 'blur' }
        ]
      },
      enviromentDialogVisible: this.URL.dialog,
      dialogVisible: false,
      templateDialog: false,
      appOptions,
      categoryOptions,
      symbolOptions,
      enviromentOptions
    }
  },
  methods: {
    // property
    updateItem (Info, index) {
      if (Info && index >= 0) {
        const item = JSON.parse(JSON.stringify(Info))
        switch (item.template) {
          case 'effect-effect':
            this.$router.push({
              name: 'EffectEffect',
              params: {
                update: index,
                value: item.value
              }
            })
            break
          case 'effect-time':
            this.$router.push({
              name: 'EffectTime',
              params: {
                update: index,
                value: item.value
              }
            })
            break
          case 'state-state':
            this.$router.push({
              name: 'StateState',
              params: {
                update: index,
                value: item.value
              }
            })
            break
          case 'event-state':
            this.$router.push({
              name: 'EventState',
              params: {
                update: index,
                value: item.value
              }
            })
            break
          case 'event-state-time':
            this.$router.push({
              name: 'EventStateTime',
              params: {
                update: index,
                value: item.value
              }
            })
            break
          case 'state-time':
            this.$router.push({
              name: 'StateTime',
              params: {
                update: index,
                value: item.value
              }
            })
            break
          case 'effect-state':
            this.$router.push({
              name: 'EffectState',
              params: {
                update: index,
                value: item.value
              }
            })
            break
          case 'effect-state-time':
            this.$router.push({
              name: 'EffectStateTime',
              params: {
                update: index,
                value: item.value
              }
            })
            break
          default:
            break
        }
      }
    },
    removeItem (index) {
      this.propertyList.splice(index, 1)
      this.refresh()
    },
    addItem () {
      if (this.beginFlag === false) {
        this.beginFlag = true
        this.TIME.time = new Date().getTime()
      }
      this.templateDialog = true
      this.update = -1
    },
    cancelAddItem () {
      this.templateDialog = false
      this.refresh()
    },
    chooseEnvironment (enviroment) {
      this.enviromentDialogVisible = false
      this.URL.dialog = false
      if (enviroment === 'Smart Office') {
        this.URL.url = 'labapi'
      } else {
        this.URL.url = 'homeapi'
      }
    },
    // property
    confimAddItem (template) {
      this.templateDialog = false
      switch (template) {
        case 'effect-effect':
          this.$router.push({
            name: 'EffectEffect',
            params: {
              update: -1,
              value: ''
            }
          })
          break
        case 'effect-time':
          this.$router.push({
            name: 'EffectTime',
            params: {
              update: -1,
              value: ''
            }
          })
          break
        case 'state-state':
          this.$router.push({
            name: 'StateState',
            params: {
              update: -1,
              value: ''
            }
          })
          break
        case 'event-state':
          this.$router.push({
            name: 'EventState',
            params: {
              update: -1,
              value: ''
            }
          })
          break
        case 'event-state-time':
          this.$router.push({
            name: 'EventStateTime',
            params: {
              update: -1,
              value: ''
            }
          })
          break
        case 'state-time':
          this.$router.push({
            name: 'StateTime',
            params: {
              update: -1,
              value: ''
            }
          })
          break
        case 'effect-state':
          this.$router.push({
            name: 'EffectState',
            params: {
              update: -1,
              value: ''
            }
          })
          break
        case 'effect-state-time':
          this.$router.push({
            name: 'EffectStateTime',
            params: {
              update: -1,
              value: ''
            }
          })
          break
      }
    },
    // 
    refresh () {
      this.$forceUpdate()
    },
    // property
    upload () {
      this.beginFlag = false
      const that = this
      if (this.propertyList.length < 1) {
        this.$message.warning('There are no properties to upload!')
        this.dialogVisible = false
        return
      }
      const finalResult = {}
      let ltl = ''
      for (let i = 0; i < this.propertyList.length; i++) {
        finalResult[i] = this.propertyList[i]
        if ('ltl' in this.propertyList[i]) {
          if (ltl === '') {
            ltl = this.propertyList[i].ltl
          } else {
            ltl = ltl + ' | (' + this.propertyList[i].ltl + ')'
          }
        }
      }
      this.$refs.form.validate((valid) => {
        if (valid) {
          that.dialogVisible = false
          axios({
            url: '/upload/saveByTemplate',
            method: 'POST',
            data: {
              user: that.form.user,
              dsl: finalResult,
              time: (new Date().getTime() - that.TIME.time) / 1000,
              rule: that.form.rule
            }
          }).then((response) => {
            that.PROPERTY.property = []
            that.propertyList = []
            that.form = {
              user: '',
              rule: ''
            }
            that.template = {
              category: '',
              template: ''
            }
          })
        }
      })
    }
  },
  mounted () {
  }
}
</script>

<style scoped>
.home-title {
  margin-top: 10px;
  margin-bottom: 10px;
}

.container,
.property {
  height: 100%;
  width: 100%;
}

.card-margin {
  margin: 10px;
}

.space-title,
.time-title {
  text-align: center;
  font-size: 18pt;
  margin-bottom: 15px;
  font-weight: bold;
  width: 91%;
  margin-bottom: 15px;
  padding: 15px 2.5%;
  letter-spacing: 2px;
  border-radius: 10px;
  border: 1px solid rgb(234, 229, 229);
  color: white;
}

.space-title {
  background-color: rgb(43, 195, 219);
}

.time-title {
  background-color: rgb(152, 210, 98);
}

.template-spatial,
.template-temporal,
.template-spatial:hover,
.template-temporal:hover {
  width: 95%;
  font-size: 13pt;
  margin-bottom: 15px;
  padding: 20px 1%;
  border-radius: 4px;
}

.template-spatial:hover,
.template-temporal:hover {
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  border: 1px solid rgb(234, 229, 229);
}
</style>
