import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import EffectEffect from '../views/EffectEffect.vue'
import StateState from '../views/StateState.vue'
import EventState from '../views/EventState.vue'
import EffectState from '../views/EffectState.vue'
import EffectStateTime from '../views/EffectStateTime.vue'
import EffectTime from '../views/EffectTime.vue'
import EventStateTime from '../views/EventStateTime.vue'
import StateTime from '../views/StateTime.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'HomeView',
    component: HomeView
  },
  {
    path: '/EffectEffect',
    name: 'EffectEffect',
    component: EffectEffect
  },
  {
    path: '/StateState',
    name: 'StateState',
    component: StateState
  },
  {
    path: '/EventState',
    name: 'EventState',
    component: EventState
  },
  {
    path: '/EffectState',
    name: 'EffectState',
    component: EffectState
  },
  {
    path: '/EffectStateTime',
    name: 'EffectStateTime',
    component: EffectStateTime
  },
  {
    path: '/EffectTime',
    name: 'EffectTime',
    component: EffectTime
  },
  {
    path: '/EventStateTime',
    name: 'EventStateTime',
    component: EventStateTime
  },
  {
    path: '/StateTime',
    name: 'StateTime',
    component: StateTime
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
