import Vue from 'vue'
import App from './App.vue'
import router from './router'
import './plugins/element.js'
import axios from 'axios'
import VueAxios from 'vue-axios'
import property from '../public/global/property'
import url from '../public/global/url'
import time from '../public/global/time'
import state from '../public/global/state'
import ltlMethod from '../public/global/ltlMethod'

Vue.prototype.$labapi = 'labapi' // 
Vue.prototype.$homeapi = 'homeapi' // 
Vue.prototype.$upload = 'upload' // 
Vue.config.productionTip = false
Vue.use(VueAxios, axios)
Vue.prototype.PROPERTY = property
Vue.prototype.URL = url
Vue.prototype.TIME = time
Vue.prototype.STATE = state
Vue.prototype.ltlMethod = ltlMethod

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
